@media only screen and (max-width: 1400px) {
 .head-top img {
  width: 600px !important;
  padding-top: 70px;
 }
 .container {
  max-width: 1100px;
 }
}

@media only screen and (max-width: 1200px) {
 .head-top img {
  width: 500px !important;
  padding-top: 70px;
 }
 .container {
  max-width: 1000px;
 }
 .shape img {
  margin-top: 50px !important;
  height: 450px !important;
 }
}

@media only screen and (max-width: 800px) {
 .section-header {
  text-align: center;
 }
 .head-top img {
  width: 330px !important;
  padding-top: 180px;
 }
 .shape img {
  height: 250px !important;
 }
 .head-top {
  padding-top: 150px;
 }
 .head-top h1 {
  text-align: center;
  font-size: 40px !important;
  font-weight: 500 !important;
 }
 .head-top p {
  text-align: center;
 }
 .btn-get-started {
  margin-top: 0;
  align-items: center;
  height: 57px;
  width: 224px;
  background-color: #2c20f1;
  padding-top: 14px;
  color: #ffffff;
  font-family: "Assistant", sans-serif;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 18px;
  border-radius: 10px;
  -webkit-transition: -webkit-transform 250ms;
  transition: -webkit-transform 250ms;
  transition: transform 250ms;
  transition: transform 250ms, -webkit-transform 250ms;
  box-shadow: 0 0 40px rgb(173, 204, 250);
 }

 .btn-get-started:hover {
  background-color: #29a6ff;
  color: white;
  -webkit-transform: translateY(-20px);
  transform: translateY(-20px);
 }
 .feature {
  padding-left: 0px !important;
  padding-top: 200px !important;
 }
 .card-feature {
  padding-top: 25px;
 }
 .section-docs {
  margin-top: 650px !important;
  height: 650px !important;
  text-align: center;
 }
 .col-docs {
  margin-top: -70px !important;
 }
 .col-docs a {
  margin-top: 30px !important;
  margin-bottom: 20px;
 }
 .section-docs img {
  margin-top: 0px;
 }
 .navbar-brand img {
  width: 80%;
 }
 .section-rich p {
  font-size: 18px;
 }
 .section-rich .col {
  padding-top: 50px;
 }
 .section-footer {
  margin-top: 400px;
 }
 .section-footer .container {
  padding-left: 30px;
 }
 .row-rich {
  margin-left: 10px;
  margin-right: 10px;
 }
 .text-rich p {
  font-size: 15px;
 }
 .row-rich .card {
  margin-top: 20px;
 }
}

